.footer {
  margin: 0 auto;
  max-width: 880px;
}

@media screen and (max-width: 1024px) {
  .footer {
    max-width: 581px;
  }
}

@media screen and (max-width: 600px) {
  .footer {
    max-width: 320px;
  }
}

@media screen and (max-width: 375px) {
  .footer {
    max-width: 282px;
  }
}
